import "./intro.scss";
import {init} from  "ityped";
import { useEffect, useRef } from "react";
export default function Intro() {
    /* Parte del codigo para la utilizacion de ityped */
   const textRef=useRef();
   useEffect(()=>{
    init(textRef.current,{
        showCursor:true,
        backDelay:1500,
        backSpeed:60,
        strings:["Developer","Designer","Creator content"],
    });
   },[]);

    return (
        <div className="intro" id="intro">
            <div className="left">
                <div className="imgContainer">
                    <img src="assets/man.png" alt="me" />

                </div>

            </div>
            <div className="right">
                <div className="wrapper">
                    <h2>Hi There, I'm</h2>
                    <h1>Eduardo Luna Meza</h1>
                    <h3>Freelancer  <span ref={textRef}></span></h3>
                </div>
                <a href="#portfolio">
                    <img src="assets/down.png" alt="" />
                </a>
            </div>
        </div>
    )
}
